.container-term-input {
  display: flex;
  gap: 6px;
  align-items: center;
  margin-bottom: 6px;
}

.term-input {
  flex: 1;
  padding: 4px 8px;
  border: 1px solid #ddd;
  border-radius: 6px;
  font-size: 14px;
}

.term-button {
  position: relative;
  bottom: 10px;
  padding: 10px 10px;
  background-color: #784bc3;
  color: white;
  border: none;
  border-radius: 6px;
  font-size: 14px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
}

.term-button:hover {
  background-color: #ae91db;
}

.term-info {
  margin-top: 6px;
  padding: 2px 8px;
  background-color: #e9f7ef;
  border: 1px solid #d4edda;
  border-radius: 6px;
  color: #155724;
  font-size: 12px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
}

.delete-button {
  position: relative;
  bottom: 5px;
  font-size: 16px;
  color: red;
  cursor: pointer;
  border: none;
  background: none;
  padding: 0;
  transition: color 0.3s ease;
}

.delete-button:hover {
  background-color: inherit;
}
