.modal-logout {
  background: #dc3545;
  color: white;
  margin-top: 20px;
}

.btn-save-edit-delete {
  display: flex;
  align-items: center;

  padding: 10px;
  gap: 6px;
}

.btn-save-edit-delete button {
  max-width: 40px;
  max-height: 40px;
  background-color: #7950f2;
}

.btn-save-edit-delete button:hover {
  max-width: 40px;
  max-height: 40px;
  background-color: #6240c9;
}

.editingInput {
  margin-top: 3px;
  width: 90%;
  border: 2px solid rgb(177, 177, 177);
  border-radius: 5px;
  height: 30px;
}
